@charset "utf-8";
@import "vars";
@import '../../node_modules/bulma/sass/utilities/_all';

.title{
  color: #75c1a2;
}

.subtitle {
  padding: 0.5em;
  color: #75c1a2;
}

.background {
  color: #bfbfbf;
  background-color: rgba($white, 0.1);
  padding: 0.5em;
  border-radius: 6px;
}

.cool-bg {
  background-image: linear-gradient(-90deg, rgba(255,255,255,0.3) 0%, rgba(0,0,0,0) 70%);
  &:nth-child(even) {
    background-image: linear-gradient(-90deg, rgba(255,255,255,0.25) 0%, rgba(0,0,0,0) 70%);
  }
}

video.background {
  position: fixed;
  z-index: -1;
  filter: blur(10px);
  object-fit: cover;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
}

p {
  text-align: justify;
}

.container {
  filter: drop-shadow(20px 20px 6px #000);
}

.logo{
  display: block;
  margin: 0 auto;
  padding: 50px 0;
  max-height: 200px;
}

#dyn-bg {
  --vanta-background-color: 0x1C1C1C;
  --vanta-color: 0x4354be;
}

.grid-container {
  display: grid;
  grid-template-areas: "why why people" "man when people" "man how how";

  @include until($tablet){
    display: block;
  }
  @include until($desktop){
    grid-template-areas: "why why people" "man when when" "how how how";
  }

  padding-bottom: 20px;
}

.man { grid-area: man; }

.people { grid-area: people; }

.why { grid-area: why; }

.how { grid-area: how; }

.when { grid-area: when; }

.container.is-fluid {
  max-width: 1400px;
}